article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}
html {
  scroll-behavior: smooth;
}
ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:after,
blockquote:before,
q:after,
q:before {
  content: "";
  content: none;
}

img {
  display: block;
  width: 100%;

  &:focus {
    outline: 0;
  }
}
input {
  &:focus {
    outline: none;
    border: 1px solid $color_input-focus;
  }
}
* {
  font-family: $cormorant;
}
b {
  font-weight: 700;
}
body {
  background-color: $fonBody;
  scroll-behavior: smooth;
}

a,
p,
div {
  color: $color_main;
  font-size: $fontSize_main;
  font-family: $cormorant;
  text-decoration: none;
}
